@import url('https://fonts.googleapis.com/css2?family=Heebo:wght@900&family=Roboto&family=Share+Tech+Mono&display=swap');

html {
  font-size: 20px;
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

a {
    text-underline-offset: 4px;
}

.absolute {
  position: absolute;
  top: 0;
  left: 0;
}

.w-full {
  width: 100%;
}

.h-full {
  height: 100%;
}

.h-min {
  min-height: 100%;
}

.page {
    position: relative;
    width: 100%;
    font-family: 'Roboto', sans-serif;
    color: white;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    box-sizing: border-box;
    padding: 20px;
}

.page-child {
    position: absolute;
    width: 720px;
    height: 1080px;
    color: white;
    font-family: 'Roboto', sans-serif;
    font-weight: 900;
    overflow: visible;
}

h1 {
  margin-top: 0;
  margin-bottom: 20px;
  padding: 0;
  font-family: 'Heebo', sans-serif;
  font-size: 5rem;
  line-height: 6rem;
  text-align: left;
}

h3 {
  margin-top: 0;
}

svg {
  vertical-align: bottom;
}

.p-10 {
  padding: 20px;
}

.job-title {
  padding: 28px;
  border: 2px solid white;
  font-size: 1.25rem;
  font-family: 'Share Tech Mono', monospace;
  font-weight: 100;
  letter-spacing: 4px;
  margin-bottom: 50px;
  border-radius: 10px;
  text-align: center;
}

.job-type {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  font-family: 'Share Tech Mono', monospace;
  font-weight: 100;
  line-height: 40px;
  margin-bottom: 0;
}

.job-type > div {
  margin: 0 20px;
}

.job-type > div:first-child {
  margin-left: 5px;
}

.job-type > div:last-child {
  margin-right: 5px;
}

.work-title {
  word-break: break-all;
  font-size: 1.25rem;
  max-width: 400px;
  padding: 28px;
  border: 2px solid white;
  font-family: 'Share Tech Mono', monospace;
  font-weight: 100;
  letter-spacing: 4px;
  margin: 0 auto 50px auto;
  border-radius: 10px;
  text-align: center;
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 500ms ease-in-out, transform 500ms ease-in-out;
}

.work-title.visible {
  opacity: 1;
  transform: translateY(0);
}

.work-type {
    font-family: 'Share Tech Mono', monospace;
    margin-top: 30px;
    font-weight: 100;
    line-height: 40px;
    margin-bottom: 0;
}

.experiences {
  margin: 0 auto;
  width: 100%;
  max-width: 450px;
  display: flex;
  flex-direction: column;
  text-align: left;
  justify-content: center;
}

.experience {
  display: flex;
  color: white;
  font-family: 'Share Tech Mono', monospace;
  font-weight: 100;
  flex: 0;
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 500ms ease-in-out, transform 500ms ease-in-out;
}

.experience.visible {
  opacity: 1;
  transform: translateY(0);
}

.experience .type {
  position: relative;
  display: inline-block;
  flex-grow: 0;
  border-radius: 50%;
  padding: 10px;
  border: 2px solid white;
  transform: scale(0);
  transition: transform 500ms ease-in-out;
  transform-origin: center;
}

.experience.visible .type {
  transform: none;
}

.experience .type img {
  width: 30px;
  height: 30px;
}

.experience .year {
  white-space: pre;
  margin-right: 15px;
  opacity: 0.7;
  line-height: 25px;
}

.experience .year-resp {
  flex-wrap: wrap;
  display: none;
  opacity: 0.7;
  line-height: 25px;
  margin-bottom: 1rem;
}

.experience .type-connector {
  display: flex;
  flex-direction: column;
}

.experience .connector {
  border-left: 4px dotted white;
  margin-left: 25px;
  padding-top: 80px;
  flex-grow: 1;
}

.experience .details {
  margin-left: 15px;
}

.experience .details .title {
  display: inline-block;
  margin-bottom: 5px;
}
.experience .details .location {
  margin-bottom: 5px;
  transition: all 0.1s ease-out;
}

.experience .details .location a {
  color: white !important;
  transition: all 0.1s ease-out;
}

.experience .details .location:hover {
  transform: translateX(5px);
  opacity: 0.7;
}

.experience .details .length {
  white-space: pre;
  opacity: 0.7;
}

.projects-list {
  list-style: none;
  padding: 0;
  opacity: 0;
  transform: translateX(-100%);
  transition: opacity 0.5s ease-in-out, transform 0.5s ease-in-out;
}

.projects-list.active {
  opacity: 1;
  transform: none;
}

.project-title {
  width: 100%;
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 500ms ease-in-out, transform 500ms ease-in-out;
}

.project-title.visible {
  opacity: 1;
  transform: translateY(0);
}

.project-title button {
  font-size: 1rem;
  text-align: left;
  width: 100%;
  font-family: 'Share Tech Mono', monospace;
  color: white;
  background-color: #22222222;
  border-radius: 50px;
  padding: 20px 25px;
  border: none;
  cursor: pointer;
  transition: all 0.2s ease-out;
  outline: none;
  pointer-events: none;
}

.project-title button:focus,
.project-title button:active,
.project-title button:hover {
  background-color: #ffffff55;
  transform: translate(5px, 0);
}

.projects-list.active .project-title button {
  pointer-events: auto;
}

.projects-list li {
  margin-bottom: 20px;
}

.projects-list li:last-child {
  margin-bottom: 0;
}

.projects-container {
  padding-top: 30px;
}

.projects {
  max-width: 400px;
  margin: 0 auto;
  position: relative;
}

.project {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  flex-direction: column;
  font-family: 'Share Tech Mono', monospace;
  font-weight: 100;
  text-align: left;
  pointer-events: none;
  opacity: 0;
  transform: translateX(100%);
  padding: 0 10px;
  transition: opacity 0.5s ease-in-out, transform 0.5s ease-in-out;
}

.project.active {
  pointer-events: auto;
  opacity: 1;
  transform: none;
}

.project p {
  margin: 0;
  margin-bottom: 15px;
}

.project p:last-child {
  margin-bottom: 0;
}

.project button {
  font-size: 1rem;
  display: inline-block;
  margin: 20px auto 0 0;
  font-family: 'Share Tech Mono', monospace;
  color: white;
  background-color: #22222222;
  border-radius: 50px;
  padding: 15px 30px;
  border: none;
  cursor: pointer;
  transition: all 0.2s ease-out;
  outline: none;
}

.project button:focus,
.project button:active,
.project button:hover {
  background-color: #ffffff55;
  transform: translate(-5px, 0);
}

.stack {
  font-size: 0.8rem;
  opacity: 0.7;
  margin-top: 25px;
}

.stack .tool {
  line-height: 30px;
  margin-right: 10px;
}

.stack .tool:last-child {
  margin-right: 0;
}

.skill-container {
  opacity: 0;
  transform: translateY(20px) scale(0.6);
  transition: opacity 500ms ease-in-out, transform 500ms ease-in-out;
}

.skill-container.visible {
  opacity: 1;
  transform: translateY(0);
}

.skill {
  font-family: 'Share Tech Mono', monospace;
  font-weight: 300;
  margin: 0 10px 20px 10px;
  text-align: center;
  border-radius: 20px;
  display: inline-block;
  background-color: #22222222;
  border-radius: 50px;
  padding: 20px;
  cursor: pointer;
  transition: all 0.2s ease-out;
}

.skill:hover {
  background-color: #ffffff55;
  transform: translateY(-5px);
}

.skills {
  max-width: 500px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  white-space: pre;
  text-align: left;
  width: 100%;
  justify-content: center;
}

.skills.centered {
  justify-content: center !important;
}

.skills-column {
    padding-left: 20px;
    padding-right: 20px;
}

img {
    vertical-align: bottom;
}

.canvas {
    position: fixed;
    top: 0;
    width: 100%;
    height: 100%;
}

.left {
    left: 0;
}

.right {
    right: 0;
}

.center {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.welcome {
  opacity: 0;
  animation-name: fade-in-anim;
  animation-duration: 0.7s;
  animation-delay: 0.15s;
  animation-timing-function: ease-out;
  animation-fill-mode: forwards;
}

.card {
  width: 100%;
  position: relative;
  padding: 60px 60px 70px 60px;
}

.letter:hover {
    opacity: 0.5;
    transform: translateY(-10px);
}

.contact-me {
  max-width: 400px;
  margin: 0 auto;
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 500ms ease-in-out, transform 500ms ease-in-out;
}

.contact-me.visible {
  opacity: 1;
  transform: translateY(0);
}

.contact-me * {
  display: flex;
  justify-content: center;
  color: white !important;
}

.contact-me a {
  margin-top: 70px;
  font-family: 'Share Tech Mono', monospace;
  background-color: #22222222;
  border-radius: 50px;
  padding: 25px 35px;
  transition: all 0.2s ease-out;
  opacity: 1 !important;
}

.contact-me a:hover {
  background-color: #ffffff55;
  transform: translateY(-5px);
}

@media only screen and (max-width: 1024px) {}

.background-gradient {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: 100% 100%;
}

.background-animated {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: opacity 1s;
    will-change: transform;
}

.background-animated.hidden {
  opacity: 0.5;
}

@media (max-aspect-ratio: 4/3) {
    .background-animated {
        object-fit: cover;
    }
  }

.fade-in {
    opacity: 0;
    animation: fade-in-anim;
    animation-duration: 1s;
    animation-delay: 0.25s;
    animation-fill-mode: forwards;
}

@keyframes fade-in-anim {
    0% {
        opacity: 0;
        transform: translateY(25px);
    }

    50% {
        transform: translateY(-5px);
    }

    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

.scroll-for-more {
    position: fixed;
    bottom: 0;
    left: 50%;
    font-family: 'Share Tech Mono', monospace;
    font-weight: bold;
    color: white;
    transform: translateX(-50%);
    padding-bottom: 20px;
    transition: all 0.5s ease-in-out;
    opacity: 0;
}

.path {
    stroke-dasharray: 50;
    animation: dash 30s linear;
    animation-iteration-count: infinite;
}

@keyframes dash {
    to {
        stroke-dashoffset: 1000;
    }
}

/* width */
::-webkit-scrollbar {
  width: 6px;
}

/* Track */
::-webkit-scrollbar-track {
  background: transparent;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: white;
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #CCC; 
}

@media screen and (max-width: 640px) {
  .experience .year {
    display: none;
  }

  .experience .year-resp {
    display: flex;
  }

  .experience {
    margin-left: 1.5rem;
  }

  .work-title {
    margin-bottom: 1.5rem;
  }
}

@media screen and (max-width: 500px) {
  html {
    font-size: 18px;
  }

  .job-title {
    padding: 20px;
  }

  .job-type > div {
    margin: 0 10px;
  }

  .project button {
    margin: 20px 0 0 auto;
  }

  h1 {
    font-size: 3rem;
    line-height: 3rem;
  }

  .skill {
    margin: 0 6px 15px 6px;
    padding: 15px;
  }
}

@media screen and (max-width: 300px) {
  h1 {
    word-break: break-all;
    font-size: 2rem;
    line-height: 2rem;
  }
}